import React, {useState} from 'react';

const CreatePostForm = ({setArticles}) => {
    const [articleName, setArticleName] = useState('');
    const [content, setContent] = useState('');

    const postArticle = async () => {
        const result = await fetch(`/api/articles/${articleName}`, {
            method: 'post',
            body: JSON.stringify({content}),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        setArticles(body);
        setArticleName('');
        setContent('');
    }

    return (
        <div id="add-comment-form">
            <h3>Write an Post:</h3>
            <label>
                Title:
                <input type="text" value={articleName} onChange={(event) => setArticleName(event.target.value)}/>
            </label>
            <label>
                Content:
                <textarea rows="4" cols="50" value={content} onChange={(event) => setContent(event.target.value)}/>
            </label>
            <button onClick={() => postArticle()}>Post Your Message</button>
        </div>
    )
}

export default CreatePostForm;